.header{
    display: flex;
}

.navbar{
    /* background-color: #111827; */
    color: rgb(176, 130, 130)  ;
    backdrop-filter: blur(7px);
    border-bottom: 1px solid;
    border-color: rgb(99, 100, 114);
}

.search{
    /* background: rgb(95, 82, 242); */
    margin-left: 10px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    min-width: 300px;
    border-radius: 20px; 
    height: 35px;
}

.searchInput{
    background: transparent;
    border: none;
    outline: none;
    padding: 10px;
    color: white;
    font-size: 18px;
    width: 500px;
}

.searchSection{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4px;
    border-radius: 10px;
    background-color: rgba(194, 197, 221, 0.4);
    backdrop-filter: blur(1px);
}

::placeholder{
    color: rgb(234, 230, 230);
}


.modal__title{
    color: blue;
}

.ModalCard{
  color: white;
  background: rgba(51, 76, 158, 0.4);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(100px);
  border-radius: 10px;
  border: 1px solid rgba(252, 252, 252, 0.18);

}

.imageInput{
    display: none;
}
