.left-side {
  width: 250px;
  height: 250px;
  /* background: #ddd; */
  display: inline-block;
  position: sticky;
  top: 15%;
}

.right-side {
  margin-left: 30px;
  width: 25%;
  background: rgba(60, 60, 68, 0.25);
  display: inline-block;
  position: sticky;
  top: 15%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  height: 100vh;
  backdrop-filter: blur(5px);

}

