.main-section {
  /* width: 1100px; */
  margin: 50px auto;
  clear: both;
  display: flex;
}

.main-section:before,
.main-section:after {
  content: '';
  display: table;
  clear: both;
}

.middle-side {
  width: 750px;
  display: inline-block;
  vertical-align: top;
  float: middle;
}


.mainProfile {
  display: flex;
  align-items: center;
  justify-content: left;
  position: relative;
  margin-top: 30px;
  cursor: pointer;

}

.profile_img {
  border: 4px solid #fff;
  overflow: hidden;
  border-radius: 50%;
}

.name {
  margin-left: 10px;

}